.loader-container {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 150px;
  height: 150px;
  margin: auto;
}
.loader {
  position: absolute;
  width: 150px;
  height: 150px;
  background-image: url("../images/Dot.png");
  background-size: contain;
}
.loader1 {
  -webkit-animation: loader1 2.5s infinite linear;
          animation: loader1 2.5s infinite linear;
}
.loader2 {
  -webkit-animation: loader2 2.5s 0.1s infinite linear;
          animation: loader2 2.5s 0.1s infinite linear;
}
.loader3 {
  -webkit-animation: loader3 2.5s 0.2s infinite linear;
          animation: loader3 2.5s 0.2s infinite linear;
}
.loader4 {
  -webkit-animation: loader4 2.5s 0.3s infinite linear;
          animation: loader4 2.5s 0.3s infinite linear;
}
.loader5 {
  -webkit-animation: loader5 2.5s 0.4s infinite linear;
          animation: loader5 2.5s 0.4s infinite linear;
}

@-webkit-keyframes loader1 {
  0% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  33% {
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
  }
  66% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}

@keyframes loader1 {
  0% {
